.main-paralax {
    $color : rgba(#000, 0.6);

    height:510px;
    background:
        linear-gradient(0deg, $color 0%, $color 100%),
        url(../images/banner-paralax.jpg) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.main-paralax-title {
    color:#fff;
    font-size:60px;
    font-weight: normal;
    text-transform: uppercase;
    font-family: 'Poiret One', cursive;
    text-align: center;
}
.main-paralax-title span {
    color: $colorPrimary;
}
.main-paralax-text {
    max-width:850px;
    width:90%;
    font-size:19px;
    color: rgba(#fff, 0.5);
    text-align: center;
}