.main-contact {
    padding:100px 0;
}
.main-contact-text {
    color:#444;
    margin-top:70px;
}

.main-contact-form {
    margin-top:70px;
}
.main-contact-form .form-row {
    margin-bottom:25px;
}
.main-contact-form .form-row-last {
    display: flex;
    align-items: center;
}
.main-contact-form label {
    position: absolute;
    left:-99999px;
}
.main-contact-form .btn {
    margin-right:30px;
}
.main-contact-form .main-contact-submit {
    font-weight: bold;
}
.main-contact-form input[type=text],
.main-contact-form input[type=email],
.main-contact-form textarea {
    border:1px solid #c5c5c5;
    padding:15px;
    display: block;
    max-width:560px;
    width:100%;
    border-radius: 6px;
    font-family: 'Nunito Sans', sans-serif;
    font-size:1rem;
    transition: 0.3s all;

    &:focus {
        border-color:#000;
        box-shadow:inset 0 0 0 1px #000;
        outline: none;
    }
    &.error {
        border-color:red;
        box-shadow:inset 0 0 0 1px red;
    }
}
.main-contact-form textarea {
    resize: none;
    max-width:100%;
    min-height: 150px;
}

@media only screen and (max-width:860px) {
    .main-contact {
        max-width: 90%;
        margin: 0 auto;
    }
    .main-contact-form input[type=text],
    .main-contact-form input[type=email],
    .main-contact-form textarea {
        max-width: 100%;
    }
}