.box {
    max-width:500px;
    margin-left:auto;
    margin-right: auto;
}
.box-photo {
    position: relative;
    display: inline-block;
    margin-bottom:20px;
}
.box-photo img {
    overflow: hidden;
    display: block;
    max-width: 100%;
    height:auto;
}
.box-photo:hover .box-photo-overlay {
    opacity:1;
}
.box-photo-overlay {
    opacity:0;
    background: rgba(#000, 0.5);
    transition: 0.5s all;
    position: absolute;
    left:0;
    top:0;
    width: 100%;
    height: 100%;
}
.box-photo-overlay:before,
.box-photo-overlay:after {
    content:'';
    width:60px;
    height:1px;
    background:rgba(#fff, 0.8);
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%, -50%);
}
.box-photo-overlay:after {
    transform: translate(-50%, -50%) rotate(-90deg);
}
.box-title {
    text-transform: uppercase;
    font-size:18px;
}
.box-content {
    margin-bottom:40px;
    color:#aaa;
}