.main-hero {
    $col1 : rgba(#000, 0.8);
    $col2 : rgba(#000, 0.85);
    height:100vh;
    background:
        linear-gradient(180deg, $col1 0%, $col1 50%, $col2 50%, $col2 100%),
        url(../images/main-banner.jpg) no-repeat;
    background-size: 5px 5px, cover;
    background-position: center bottom;
    display: flex;
}

.main-hero .container {
    flex-grow:1;
    display: flex;
    flex-direction: column;
}

.main-hero-bar {
    position:absolute;
    bottom:-10px;
    left:0;
    width:100%;
    height:51px;
    overflow: hidden;

    span {
        margin:0 auto;
        background: url(../images/banner-bottom.svg);
        background-repeat: no-repeat;
        background-position: center bottom;
        background-size: 100% 100%;
        height:51px;
        width:263px;
        display: block;
        position: absolute;
        left:50%;
        bottom:0;
        transform: translateX(-50%);
    }

    &:before {
        right:calc(50% + 100px);
        height:51px;
        background:#fff;
        width:99999px;
        content:'';
        position: absolute;
        bottom:0;
    }
    &:after {
        left:calc(50% + 100px);
        height:51px;
        background:#fff;
        width:99999px;
        content:'';
        position: absolute;
        bottom:0;
    }
}





